import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { myOverdueDebtComponent }   from './views/myOverdueDebt/myOverdueDebt.component';
import { taskListComponent }   from './views/taskList/taskList.component';
/*import { HeroesComponent }      from './heroes.component';
import { HeroDetailComponent }  from './hero-detail.component';*/

const routes: Routes = [
  { path: '', redirectTo: '/myOverdueDebt', pathMatch: 'full' },
  { path: 'taskList',  component: taskListComponent },
  /*{ path: 'detail/:id', component: HeroDetailComponent },*/
  { path: 'myOverdueDebt',     component: myOverdueDebtComponent }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


