import { NgModule } from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { AppComponent } from './app.component';
import { AppRoutingModule }     from './route';
import { myOverdueDebtComponent }   from './views/myOverdueDebt/myOverdueDebt.component';
import { taskListComponent }   from './views/taskList/taskList.component';
@NgModule({
  imports: [
    BrowserModule,
    AppRoutingModule
  ],
  declarations: [
    AppComponent,
    taskListComponent,
    myOverdueDebtComponent
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



